<template>
  <div class="bs-task">
    <el-table :data="list" border style="width: 100%">
      <el-table-column fixed prop="create_time" label="发布日期" width="200"></el-table-column>
      <el-table-column prop="title" label="任务名称" width="120"></el-table-column>
      <el-table-column prop="submit_username" label="发布人" width="120"></el-table-column>
      <el-table-column prop="status_name" label="状态" width="120"></el-table-column>
      <el-table-column prop="book_name" label="任务类型" width="200"></el-table-column>
      <el-table-column prop="content" label="任务内容" width="420"></el-table-column>
      <el-table-column fixed="right" label="操作" width="200">
        <template slot-scope="scope">
          <el-button @click="handleClick(scope.row)" type="text" size="small">查看</el-button>
          <el-button type="text" @click="handleModify(scope.row)" size="small">修改状态</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import * as ajax from "@/api";

export default {
  methods: {
    handleClick(row) {
      this.$alert(
        `<strong>发布人：${row.submit_username}</strong>
        <p>任务状态:${row.status_name}</p>
        <p>所属类型:${row.book_name}</p>
        <p>发布时间:${row.create_time}</p>
        <p>标题:${row.title}</p>
        <p>技术点:${row.type_name}</p>
        <p>发布内容:${row.content}</p>
                `,
        "任务信息",
        {
          dangerouslyUseHTMLString: true
        }
      );
    },
    handleModify(row){
        let id = row.status_id==1 ? 2 : 1
        ajax.modifyTaskStatus(row.id,id).then(resp=>{
            if(resp.data.code){
                for(var i=0;i<this.list.length;i++){
                    if(resp.data.data.task_info.id == this.list[i].id){
                        this.list[i].status_name = resp.data.data.task_info.status_name
                        this.list[i].status_id = resp.data.data.task_info.status_id
                    }
                }
            }
            
        })
    }
  },
  created() {
    ajax.getTaskList().then(resp => {
      if (resp.data.code) {
        this.list = resp.data.data.task_list;
      }
    });
  },
  data() {
    return {
      list: []
    };
  }
};
</script>

<style lang='scss'>
.bs-public-task {
  flex: 1;
}
</style>